import { Injectable } from '@angular/core';

@Injectable()
export class ConfigService {
  public hostname = window.location.protocol + "//" + window.location.hostname + ":" + 3000;
  // public hostname = window.location.protocol + "//" + "192.168.100.1" + ":" + 3000;

}



