import { platformBrowserDynamic } from '@angular/platform-browser-dynamic';
import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router'
import {Http} from '@angular/http';
import {ConfigService} from '../../share/config.service'
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';


@Component({
  selector: 'app-graph',
  templateUrl: './graph.component.html',
  styleUrls: ['./graph.component.css']
})
export class GraphComponent implements OnInit {

  public soilChartData:any = [];
  public lightChartData:any = [];
  public vpdChartData:any = [];
  public tempChartData:any = [];
  public humiChartData:any = [];

  public soilCharOption:any = {
        title: 'Soil',
        vAxis: { minValue: 0 },
        legend: {
            position: 'bottom'
        }
    };
  public sensorsData:any = [];

  sensors:Observable<any>;
  soil:any;
  vpd:any;
  light:any;
  humi:any;
  temp:any;
  chart : any;
  
  constructor(private route:ActivatedRoute, private http:Http,private config:ConfigService) { }
  
  ngOnInit() {
     
    this.route.queryParams.subscribe(params=>{
      var filename = params['filename'];
      this.http.get(this.config.hostname + "/api/getdata?date="+filename).subscribe(data=>{
        var d = data.json();

        d.forEach(val=>{
          this.soilChartData.push([val.datetime,val.soil]);
          this.vpdChartData.push([val.datetime,val.vpd]);
          this.tempChartData.push([val.datetime,val.temp]);
          this.humiChartData.push([val.datetime,val.humi]);
          this.lightChartData.push([val.datetime,val.light]);
        })

        

        this.soil = {
            chart: {
              zoomType: 'xy',
              type: 'spline'
            },
            title : { text : 'Soil moiseture' },
            series : [{
            animation: false,
            type: 'line',
                data: this.soilChartData,
            }],
            xAxis : {
              type: 'datetime'
            }
        };

        this.vpd = {
            chart: {
              zoomType: 'xy',
              type: 'spline'
            },
            title : { text : 'VPD' },
            series : [{
            animation: false,
            type: 'line',
                data: this.vpdChartData,
            }],
            xAxis : {
              type: 'datetime'
            }
        };

        this.light = {
            chart: {
              zoomType: 'xy',
              type: 'spline'
            },
            title : { text : 'Light' },
            series : [{
            animation: false,
            type: 'line',
                data: this.lightChartData,
            }],
            xAxis : {
              type: 'datetime'
            }
        };

        this.temp = {
            chart: {
              zoomType: 'xy',
              type: 'spline'
            },
            title : { text : 'Temperature' },
            series : [{
            animation: false,
            type: 'line',
                data: this.tempChartData,
            }],
            xAxis : {
              type: 'datetime'
            }
        };

        this.humi = {
            chart: {
              zoomType: 'xy',
              type: 'spline'
            },
            title : { text : 'Humidity' },
            series : [{
            animation: false,
            type: 'line',
                data: this.humiChartData,
            }],
            xAxis : {
              type: 'datetime'
            }
        };

      })
    })
  }

  
  issoil:boolean = true;
  isvpd:boolean = false;
  istemp:boolean = false;
  ishumi:boolean = false;
  islight:boolean =false;

  chooseGraph(val):void{
    switch (val) {
      case "soil":
        this.clearGraph();
        this.issoil = true;
        break;
      case "vpd":
        this.clearGraph();
        this.isvpd = true;
        break;
      case "temp":
        this.clearGraph();
        this.istemp = true;
        break;
      case "humi":
        this.clearGraph();
        this.ishumi = true;
        break;
      case "light":
        this.clearGraph();
        this.islight = true;
        break;
      case "all":
        this.clearGraph();
        this.issoil = true;
        this.isvpd = true;
        this.istemp = true;
        this.ishumi = true;
        this.islight =true;
        break;
    
      default:
        break;
    }

  }

  clearGraph():void{
    this.issoil = false;
    this.isvpd = false;
    this.istemp = false;
    this.ishumi = false;
    this.islight =false;
  }

}



