import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {Http} from '@angular/http';
import {ConfigService} from '../../share/config.service'

@Component({
  selector: 'app-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.css']
})
export class TableComponent implements OnInit {

  constructor(private route:ActivatedRoute,private http:Http,private config:ConfigService) { }
  filename:string;
  sensorsData:any;
  ngOnInit() {
    this.route.queryParams.subscribe(params=>{
      // console.log(params);
      this.filename  = params['filename'];
       this.http.get(this.config.hostname + "/api/getdata?date="+this.filename)
      .subscribe((data)=>{
        // this.dateList = data.json();
        // console.log(data.json())
        this.sensorsData = data.json();
      },(err)=>{
        console.log(err);
      })
    })
  }

}



