import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { LiveDataComponentComponent } from './live-data-component/live-data-component.component';
import { SocketService} from './share/socket.service'
import { DataService} from './share/data.service';
import { SetPointComponent } from './set-point/set-point.component'

import { AppRoutingModule } from './app-routing/app-routing.module';
import { DisconnectComponent } from './disconnect/disconnect.component';
import { ModeComponent } from './mode/mode.component';
import { UiSwitchModule} from '../../node_modules/angular2-ui-switch/src';
import { ControlComponent } from './control/control.component';
import {ModeService} from './mode/mode.service';

import { ManualComponent } from './manual/manual.component';
import { AutoComponent } from './auto/auto.component';
import { TimerComponent } from './timer/timer.component'
import * as $ from 'jquery';
import { DetectingComponent } from './detecting/detecting.component';
import {ConnectionGuard} from './disconnect/connection-guard';
import { SettingsComponent } from './settings/settings.component';
import { DataComponent } from './data/data.component';
import { ChartsModule } from 'ng2-charts';
import {GraphService} from './data/graph.service'
import { SensortableComponent } from './sensortable/sensortable.component';
import { TableComponent } from './sensortable/table/table.component';
import { GraphComponent } from './sensortable/graph/graph.component';
import {GoogleChart} from 'angular2-google-chart/directives/angular2-google-chart.directive';
import {ConfigService} from './share/config.service'

import { ChartModule } from 'angular2-highcharts';
import { HighchartsStatic } from 'angular2-highcharts/dist/HighchartsService';
import * as highcharts from 'highcharts';
import { RealtimegraphComponent } from './live-data-component/realtimegraph/realtimegraph.component';
export function highchartsFactory() {
  return highcharts;
}

@NgModule({
  declarations: [
    AppComponent,
    LiveDataComponentComponent,
    SetPointComponent,
    DisconnectComponent,
    ModeComponent,
    ControlComponent,
    ManualComponent,
    AutoComponent,
    TimerComponent,
    DetectingComponent,
    SettingsComponent,
    DataComponent,
    SensortableComponent,
    TableComponent,
    GraphComponent,
    GoogleChart,
    RealtimegraphComponent
    
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    AppRoutingModule,
    UiSwitchModule,
    ChartsModule,
    ChartModule,
    
  ],
  providers: [SocketService, DataService, ModeService, ConnectionGuard,GraphService,ConfigService,
    {
      provide: HighchartsStatic,
      useFactory: highchartsFactory
    }],
  bootstrap: [AppComponent]
})
export class AppModule { }



