import { Component, OnInit } from '@angular/core';
import {SocketService} from '../share/socket.service';
import {DataService} from '../share/data.service';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit {
  yearList:any = []
  monthList:any= []
  dayList:any = [];
  hourList:any = [];
  minList:any = [];
  pList:any = ["am", "pm"];

  day:number = 1;
  month:number =1;
  year:number=2015;

  hour:number=0;
  min:number=0;
  p:string = "am";

  constructor(private io:SocketService, private dataservice:DataService) { }

  ngOnInit() {
    var year = 2015;

    for(var i = 1 ; i <= 16; i++){
      this.yearList.push(year++);
    }

    for(var i = 1 ; i <= 12; i++){
      this.monthList.push(i);
    }

    for(var i = 1 ; i <= 31; i++){
      this.dayList.push(i);
    }

    this.hourList.push(12);
    for(var i = 1 ; i <= 11; i++){
      this.hourList.push(i);
    }

    for(var i = 0 ; i <= 59; i++){
      this.minList.push(i);
    }
  }

  onUpdate():void{
    var hour = Number(this.hour);

    if(this.p == "pm"){
      if(hour != 12)
        hour = hour + 12;
    }
    else{
      if(hour == 12)
        hour = 0;
    }

    var datetime = {
      'day': Number(this.day),
      'month': Number(this.month),
      'year': Number(this.year),
      'hour': Number(hour),
      'min': Number(this.min)
    }

    console.log(datetime);
    this.io.socket.emit("DATETIME", datetime);
  }

}



