import { Component, OnInit } from '@angular/core';
import { DataService } from '../share/data.service'
import {Router} from '@angular/router';
@Component({
  selector: 'app-live-data-component',
  templateUrl: './live-data-component.component.html',
  styleUrls: ['./live-data-component.component.css']
})
export class LiveDataComponentComponent implements OnInit {

  constructor(public data:DataService, private router:Router) { 
    
  }

  ngOnInit() {
    
  }

  toGraph():void{
    this.router.navigate(['live/realtime']);
  }

  toTable():void{
    this.router.navigate(['live']);
  }




}



