import { Component, OnInit } from '@angular/core';
import {ActivatedRoute,Router} from'@angular/router'
import {Http} from '@angular/http';

@Component({
  selector: 'app-sensortable',
  templateUrl: './sensortable.component.html',
  styleUrls: ['./sensortable.component.css']
})
export class SensortableComponent implements OnInit {
  sensorsData:any;
  filename:string;
  constructor(private route:ActivatedRoute,private http:Http,private router:Router) { }
  
  ngOnInit() { 
    this.route.queryParams.subscribe(params=>{
      this.filename = params['filename'];
       this.router.navigate(['datalogger/graph'], { queryParams: { filename: this.filename }});
    })
  }

  toTable():void{
    this.router.navigate(['datalogger/table'], { queryParams: { filename: this.filename } });
  }

  toGraph():void{
    this.router.navigate(['datalogger/graph'], { queryParams: { filename: this.filename } });
  }
}



